@import url("https://fonts.googleapis.com/css?family=Karla:400,700|Playfair+Display:400,700,900");
body {
  font-family: "Karla", sans-serif;
  font-weight: normal;
  font-style: normal;
  font-size: 14px;
}

.img {
  max-width: 100%;
  transition: all 0.3s ease-out 0s;
}

a,
.btn {
  -webkit-transition: all 0.3s ease-out 0s;
  -moz-transition: all 0.3s ease-out 0s;
  -ms-transition: all 0.3s ease-out 0s;
  -o-transition: all 0.3s ease-out 0s;
  transition: all 0.3s ease-out 0s;
}

.display-inline {
  display: inline-block;
}

.btn {
  border-radius: 0px;
  font-size: 14px;
  padding: 0px 15px;
  height: 30px;
  line-height: 30px;
}

a:focus,
.btn:focus {
  text-decoration: none;
  outline: none;
}

a:focus,
a:hover {
  color: #464646;
  text-decoration: none;
}

a,
button,
input {
  outline: medium none;
  color: #464646;
}

.uppercase {
  text-transform: uppercase;
}

.capitalize {
  text-transform: capitalize;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Playfair Display", serif;
  font-weight: normal;
  color: #363636;
  margin-top: 0px;
  font-style: normal;
  font-weight: 400;
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  color: inherit;
}

h1 {
  font-size: 40px;
  font-weight: 500;
}

h2 {
  font-size: 35px;
}

h3 {
  font-size: 28px;
}

h4 {
  font-size: 22px;
}

h5 {
  font-size: 18px;
}

h6 {
  font-size: 16px;
}

ul {
  margin: 0px;
  padding: 0px;
}

li {
  list-style: none;
}

p {
  font-size: 14px;
  font-weight: normal;
  line-height: 24px;
  color: #626262;
  margin-bottom: 15px;
}

hr {
  margin: 60px 0;
  padding: 0px;
  border-bottom: 1px solid #eceff8;
  border-top: 0px;
}

hr.style-2 {
  border-bottom: 1px dashed #f10;
}

hr.mp-0 {
  margin: 0;
  border-bottom: 1px solid #eceff8;
}

hr.mtb-40 {
  margin: 40px 0;
  border-bottom: 1px solid #eceff8;
  border-top: 0px;
}

label {
  font-size: 15px;
  font-weight: 400;
  color: #626262;
}

*::-moz-selection {
  background: #4fc1f0;
  color: #fff;
  text-shadow: none;
}

::-moz-selection {
  background: #4fc1f0;
  color: #fff;
  text-shadow: none;
}

::selection {
  background: #4fc1f0;
  color: #fff;
  text-shadow: none;
}

.height-100vh {
  height: 100vh;
}

.mark,
mark {
  background: #4fc1f0 none repeat scroll 0 0;
  color: #ffffff;
}

span.tooltip-content {
  color: #00a9da;
  cursor: help;
  font-weight: 600;
}

.f-left {
  float: left;
}

.f-right {
  float: right;
}

.fix {
  overflow: hidden;
}

.browserupgrade {
  margin: 0.2em 0;
  background: #ccc;
  color: #000;
  padding: 0.2em 0;
