/*************************
        Input
*************************/

input {
  background: #eceff8;
  border: 2px solid #eceff8;
  height: 45px;
  box-shadow: none;
  padding-left: 10px;
  font-size: 14px;
  color: #626262;
  width: 100%;
}

select {
  width: 100%;
  background: #eceff8;
  border: 2px solid #eceff8;
  height: 45px;
  padding-left: 10px;
  box-shadow: none;
  font-size: 14px;
  color: #626262;
}

option {
  background: #fff;
  border: 0px solid #626262;
  padding-left: 10px;
  font-size: 14px;
}

input:focus {
  background: transparent;
  border: 1px solid #464646;
}

textarea {
  resize: vertical;
  background: #eceff8;
  border: 2px solid #eceff8;
  padding: 10px;
  width: 100%;
  font-size: 14px;
}

textarea:focus {
  background: transparent;
  border: 2px solid #4fc1f0;
  outline: none;
}

::-moz-placeholder {
  color: #444;
  font-size: 14px;
}

.input-group.divcenter.input-group .form-control {
  padding-left: 0px;
}

/*************************
        back-to-top
*************************/

#back-to-top .top {
  z-index: 999;
  position: fixed;
  margin: 0px;
  color: #fff;
  transition: all 0.5s ease-in-out;
  position: fixed;
  bottom: 105px;
  right: 15px;
  border-radius: 3px;
  z-index: 999;
  background: transparent;
  font-size: 14px;
  background: #4fc1f0;
  width: 40px;
  height: 40px;
  text-align: center;
  line-height: 38px;
}

#back-to-top .top:hover {
  color: #fff;
  height: 50px;
}

#back-to-top .top {
  background: #007da1;
}

